import React from "react";
//import { Text, View } from "react-native";

import Item from './item'

const defaultData = [{
  _id: '1234',
  name: 'theo',
  role: 'dev'
}]

const List = ({data=defaultData}) => {
  console.log('data:', data)
    const characters = data.slice(0,10)
    console.log('cha:', characters)
    const list = characters.map(character => <Item character={character} key={character._id} />)
    return (
      <div style={{height: 'auto', backgroundColor: 'powderblue'}}>
        <span className="title">Hary potter Characters</span>
        <div>
          {list}
        </div>
      </div>
    );
  };

export default List
