import React, {Component} from 'react';
import ReactLogo from './ReactLogo.svg';
import YarnCat from './YarnCat.svg';
import './App.css';

import {CompOne, CompTwo, CompThree, List, Button} from '@project/components';
import {getData} from '@project/api';
import Footer from '@project/footer';
 
class App extends Component {
	state = {data:[]}

	componentWillMount(){
		getData()
		.then(data => data)
		.then(data =>
		{	
			this.setState({data: JSON.parse(data)});
			}
		)
		.catch(error => console.log('error:', error))
	}

	render() {
		return (
			<div className="App">
				<header className="App-header">
					<Footer />
						<List data={this.state.data} />
						<Button />
					<div>
						<img
							src={ReactLogo}
							className="React-logo"
							alt="React Logo"
						/>
						<img
							src={YarnCat}
							className="Yarn-cat"
							alt="Yarn Workspaces Cat"
						/>
					</div>
					<h1>
						<a
							className="App-link"
							href="https://github.com/react-workspaces/cra-workspaces-playground"
							target="_blank"
							rel="noopener noreferrer"
						>
							<strong>React</strong> Workspaces!!
						</a>
					</h1>
					<h2>Hot Reload Your Workspaces</h2>
					<p className="body">
						<code className="file">
							packages/apps/app-one/src/App.js
						</code>
						<code className="file">
							packages/components/src/CompOne/CompOne.js
						</code>
						<code className="file">
							packages/components/src/CompTwo/CompTwo.js
						</code>
						<code className="file">
							packages/components/src/CompThree/CompThree.js
						</code>
					</p>
					<div className="components">
						<CompOne />
						<CompTwo />
						<CompThree />
					</div>
				</header>
			</div>
		);
	}
}

export default App;
